.header_x{
    text-align: center;
    color: rgb(6, 185, 6);
    font-size: 3rem;
}

.header_0{
    text-align: center;
    color: yellow;
    font-size: 3rem;
}

.header{
    text-align: center;
    color: white;
}

.container {
    display: flex;
    align-items: center;
    justify-content: space-around;
    
}

.scorex{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
   width: 15%;
    aspect-ratio: 1/1;
    text-align: center;
    color: rgb(6, 185, 6);
    border: 2px solid rgb(6, 185, 6);
    padding: 0 3px;
    box-shadow: 0px 0px 30px 2px;
    text-wrap: wrap;
}

.scorex > p{
    font-size: 1.8vw;
}

.score0{
    display: flex;
  width: 15%;
  aspect-ratio: 1/1;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: yellow;
    border: 2px solid yellow;
    box-shadow: 0px 0px 30px 2px ;
    padding: 0 3px;
    text-wrap: wrap;
    
}

.score0 > p{
    font-size: 1.8vw;
}

.board {
    
    
    margin-bottom: 40px;
    padding: 5px;
    width: 33%;
    aspect-ratio: 1/1;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 5px;
}

.square_x {
    aspect-ratio: 1 / 1;
    background-color: rgb(8, 58, 58);
    display: flex;
    color: rgb(6, 185, 6);
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    cursor: pointer;
}

.cell {
    
    display: flex;
    justify-content: center;
    align-items: center;
width: 100%;
height: 100%;
}

p{
    font-size: clamp(2.5rem, 8vw, 10rem)
}

.square_0 {
    aspect-ratio: 1 / 1;
    background-color: rgb(8, 58, 58);
    display: flex;
    color: yellow;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    cursor: pointer;
}

.winner {
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 3rem;
    width: 80%;
    color: white;
    font-size: 2rem;
}

.winner_x{
    color: rgb(6, 185, 6);
}

.winner_0{
    color: yellow;
}
.reset-container {
    padding-top: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.reset {
    width: 25%;
    font-size: 3vw;
    padding: 3px 20px;
    color: red;
    border-radius: 10px;
    box-shadow: 5px 5px 30px grey;
    cursor: pointer;
}



@media screen and (max-width: 768px) {
    .board{
        width: 45%;
    }
    p{
        font-size: clamp(1rem, 8vw, 5rem)
    }
    .board{
        margin-top: 30px;
    }
    .winner{
        font-size: 1.45rem;
    }
    .score0{
        width: 17%;
    }
    .scorex{
        width: 17%;
    }
.scorex > p{
    font-size: 3.5vw;
}
.score0 > p{
    font-size: 3.5vw;
}


    .reset{
    font-size: 6vw;
    width: 40%;
    padding: 10px 5px;
    }
    
}